@use "../../../style/colors";
@use "../../../style/util";

a {
  text-decoration: none;
  color: inherit;
}
.Button {
  background-color: colors.$blue_c02;
  color: colors.$white_c01;
  font-size: util.rem(17px);
  font-weight: 600;
  border-radius: 15px;
}
.HoverButtonWrap {
  margin-top: util.rem(50px);
  @media screen and (max-width:util.rem(991px)){
    display: flex;
    justify-content: center;
    margin-top: util.rem(40px);
  }
}
.HoverButton {
  padding: util.rem(16px) util.rem(30px);
  @media screen and (max-width:util.rem(991px)){
    padding: util.rem(14px) util.rem(30px);
    font-size: util.rem(14px);
  }
}
.HoverButton:hover {
  color: inherit;
}
.CommonButtonWrap {
  margin-top: util.rem(70px);

  @media screen and (max-width:util.rem(991px)){
    margin: util.rem(50px);
  }
}
.CommonButton {
  padding: util.rem(17px) util.rem(40px);
  font-weight: 700;

  @media screen and (max-width:util.rem(991px)){
    padding: util.rem(14px) util.rem(30px);
    font-size: util.rem(14px);
  }
}

